// Color
$white: #fff !default;
$gray-100: #f6f7f8 !default;
$gray-300: #dbe0e4 !default;
$gray-400: #c1cad1 !default;
$gray-500: #9eacb7 !default;
$gray-600: #828d96 !default;
$gray-700: #656e75 !default;
$gray-900: #484f54 !default;
$black: #000 !default;

$blue-100: #eaf3fa !default;
$blue-300: #accfec !default;
$blue-400: #6eacde !default;
$blue-500: #1f8be2 !default;
$blue-600: #1767a7 !default;
$blue-700: #125082 !default;
$blue-900: #104570 !default;

$topaz-500: #ffcb6e !default;

$coral-500: #ff835d !default;

$mulberry-500: #be5486 !default;

$middle-blue-purple-100: #f4f2f8 !default;
$middle-blue-purple-300: #d4cbe4 !default;
$middle-blue-purple-400: #b4a5cf !default;
$middle-blue-purple-500: #8a72b5 !default;
$middle-blue-purple-600: #715e95 !default;
$middle-blue-purple-700: #584974 !default;
$middle-blue-purple-900: #3f3453 !default;

$independence-100: #eef0f2 !default;
$independence-300: #bec3cb !default;
$independence-400: #8d96a5 !default;
$independence-500: #4d5a72 !default;
$independence-600: #404a5e !default;
$independence-700: #323a49 !default;
$independence-900: #232934 !default;

$weldon-blue-100: #f2f5f8 !default;
$weldon-blue-300: #cbd7e3 !default;
$weldon-blue-400: #a4bacf !default;
$weldon-blue-500: #7193b4 !default;
$weldon-blue-600: #5d7994 !default;
$weldon-blue-700: #485e73 !default;
$weldon-blue-900: #344352 !default;

$font-color: $gray-900;
$background-color: $gray-100;
$primary: $blue-500 !default;
$secondary: $gray-500 !default;

// Brands Color

$skype: #00aff0;
$facebook: #3f51b5;
$linkedin: #0073b1;
$angular: #dd0031;
$nodejs: #689f63;
$reactjs: #61dafb;
$redux: #764abc;
$html5: #e44d26;
$css3: #264de4;
$es6: #ffcc33;
$sass: #cd6799;
$git: #f05033;

// Spacing

$spacer: 1rem !default;

// Grid breakpoints
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px
) !default;

$grid-breakpoint-lg: map-get($grid-breakpoints, "lg");

// Typography

$font-family-base: "Lato", sans-serif;

$font-size-base: 1rem !default;

$h1-font-size: $font-size-base * 2.5 !default;
$h2-font-size: $font-size-base * 2 !default;
$h3-font-size: $font-size-base * 1.75 !default;
$h4-font-size: $font-size-base * 1.5 !default;
$h5-font-size: $font-size-base * 1.25 !default;
$h6-font-size: $font-size-base !default;

$font-weight-light: 300 !default;
$font-weight-normal: 400 !default;
$font-weight-bold: 700 !default;

$font-weight-base: $font-weight-normal !default;
$line-height-base: 1.5 !default;

$headings-margin-bottom: $spacer / 2 !default;
$headings-font-family: null !default;
$headings-font-weight: 500 !default;
$headings-line-height: 1.2 !default;
$headings-color: null !default;

// Components
$border-width: 1px !default;
$border-color: $gray-300 !default;
$border-radius: 0.25rem !default;

$border: $border-width solid $border-color;

$box-shadow: 0 0.5rem 1rem rgba($black, 0.35) !default;

// Buttons
$btn-padding-y: 0.375rem !default;
$btn-padding-x: 0.75rem !default;

$btn-font-size: $font-size-base !default;
$btn-font-family: $font-family-base !default;
$btn-line-height: $line-height-base !default;
$btn-white-space: nowrap !default;
$btn-font-weight: $font-weight-normal !default;

$btn-border-width: $border-width !default;
$btn-border: $btn-border-width solid transparent !default;
$btn-border-radius: $border-radius !default;
$btn-box-shadow: inset 0 1px 0 rgba($white, 0.15), 0 1px 1px rgba($black, 0.075) !default;
$btn-focus-box-shadow: 0 0 0 0.2rem rgba($primary, 0.25) !default;
$btn-active-box-shadow: inset 0 3px 5px rgba($black, 0.125) !default;

$btn-disabled-opacity: 0.65 !default;

$btn-transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
  border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out !default;

// Cards
$card-box-shadow: $box-shadow;

// Timeline
$timeline-box-shadow: $box-shadow;
$timeline-left-arrow-shaddow: -1px 1px 1px rgba($black, 0.2);
$timeline-right-arrow-shaddow: 1px -1px 1px rgba($black, 0.2);

$timeline-colors: (
  independence: $independence-500,
  weldon: $weldon-blue-500,
  purple: $middle-blue-purple-500,
  mulberry: $mulberry-500,
  coral: $coral-500,
  topaz: $topaz-500
) !default;

// Title
$title-align: center !default;
$title-font-size: 2.18em !default;
